export default {
	data() {
		return {
			nutrDetailList:'',
			urlV:''+new Date().getTime()
		}
	},
	onLoad(option){
		let { id } = option
		this.$request({
			url:"/front/food/queryNutritionInfo",
			data:{
				 nutritionId:id
			}
		}).then(res=>{
			this.nutrDetailList=res.data.data
		})
		
		
		
	},
	methods: {
		
	}
}
